.up-button {
    position: fixed;
    width: 35px;
    bottom: 60px;
    height: 40px;
    right: 20px;
    background-color: transparent;
    background-image: url("../img/arrow.svg");
    background-size: contain;
    background-repeat: no-repeat;
    border: none;
    color: $white;
    font-size: 24px;
    text-align: center;
    padding-bottom: 10px;
    box-sizing: border-box;
  }